import styled from "styled-components";
import {theme} from "../styles/Theme";

export const Button = styled.button`
    width: 170px;
    height: 32px;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    align-self: center;
    color: ${theme.colors.font};
`